<template>
  <div class="section">
    <div class="columns">
      <div class="column is-offset-2 is-8">
        <div class="notification is-success is-my-6">
          <b>Chamada Gemidão é o serviço que realiza ligações para qualquer número (fixo e móvel) do Brasil e ao atender
            o aúdio mais famoso do Whatsapp é tocado.<br>
          </b>
        </div>
        <div class="notification is-info is-my-6">
          <b>Selecione o pacote desejado, realize o pagamento via PagSeguro, quando o pagamento for aprovado o crédito será liberado e voce receberá um SMS com a senha para o acesso.
            <br>Quando o crédito estiver disponível, insira o número que irá aparecer na bina (exatamente, você pode alterar o numero que será exibido) e o número do destinatário, após 2 minutos que o destinatário atender ele irá receber um SMS com o número real do remetente.
            <br>Após realizar a ligação, o extrato de ligações exibirá o status da chamada, "atendida" significa que a ligação foi atendida pelo destinatário, caso outro status seja exibido o saldo da ligação é devolvido.
          </b>
        </div>
        <div v-if="getTermos()">
          <div class="field is-horizontal">
            <div class="field-body">
              <div class="field">
                <p class="control">
                  <input type="number" placeholder="Meu celular com ddd" v-model="meuNumero" class="input">
                </p>
              </div>
              <div class="field">
                <p class="control">
                  <input type="number" placeholder="Insira novamente" v-model="meuNumero2" class="input">
                </p>
              </div>
              <div class="field">
                <p class="control has-icons-right">
                  <span class="select">
                    <select v-model.number="planoEscolhido" >
                      <option v-for="(p, i) of planos" :value="i">{{i}} {{i == 1 ? 'Ligação': 'Ligações'}} - R${{p}}</option>
                    </select>
                  </span>
                </p>
              </div>
              <div class="field">
                <div class="control">
                  <button class="button is-success button-max" name="numeroLogin" :disabled="!meuNumero.match('^[0-9]{10,11}$') || meuNumero != meuNumero2" :class="{'is-loading': comprando}" @click="comprando = true; comprar(); eventoGa('compra', 'plano '+ planoEscolhido)">Comprar</button>
                </div>
              </div>
            </div>
          </div>
          <div>
            <div v-if="!auth.token" class="field is-horizontal">
              <div class="field-body">
                <div class="field">
                  <p class="control">
                <input class="input" type="number" placeholder="Meu numero" v-model="auth.numero">
              </p>
                </div>
                <div class="field">
                  <p class="control">
                <input class="input" type="password" placeholder="Senha" v-model="auth.senha">
              </p>
                </div>
                <div class="field">
                  <p class="control">
                <button class="button is-success button-max" :disabled="!auth.numero.match('^[0-9]{10,11}$') || auth.senha == ''" :class="{'is-loading': auth.autenticando}" @click="autenticar(); eventoGa('autenticacao', auth.numero);">Autenticar</button>
              </p>
                </div>
              </div>
            </div>
            <div v-else>
              <button class="button is-danger" @click="eventoGa('remover-autenticacao', info.numero); removerAutenticacao();">Remover Autenticação</button>
            </div>
          </div>
          <div v-if="info.numero">
            <br>
            <div class="notification is-primary">
              <div>
                <b>{{info.numero}} saldo de {{info.saldo}} {{info.saldo > 1 ? 'Ligações': 'Ligação'}}</b>
                <button class="button is-warning is-small" :class="{'is-loading': infoAtualizando}" @click="getInfo(); getTransacoes(); eventoGa('atualizar-saldo', info.numero + ' - ' + info.saldo);">Atualizar Saldo e Histórico</button>
              </div>

            </div>
          </div>
          <div v-if="auth.token">
            <div><br>
              <div class="field is-horizontal">
                <div class="field-body">
                  <div class="field">
                    <p class="control">
                      <input name="de" class="input" type="number" v-model="chamada.de" placeholder="Numero com ddd do remetente (bina)">
                    </p>
                  </div>
                  <div class="field">
                    <p class="control">
                      <input name="para" class="input" type="number" v-model="chamada.para" placeholder="Numero com ddd do destinatário">
                    </p>
                  </div>
                  <div class="field">
                    <p class="control">
                      <button class="button is-success button-max" :class="{'is-loading': chamada.enviando}" :disabled="!chamada.de.match('^[0-9]{10,11}$') || !chamada.para.match('^[0-9]{10,11}$') || info.saldo <= 0" @click="ligar(); eventoGa('ligar', info.numero + ' de: '+ chamada.de + ' para: '+ chamada.para);">Ligar</button>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-if="transacoes && transacoes.length"><br>
            <div class="box" v-for="t of transacoes">
              <div class="media">
                <div class="media-content">
                  <div class="content has-text-centered">
                    <div class="element">De: <b>{{t.ligacao.de}}</b></div>
                    <div class="element">Para: <b>{{t.ligacao.para}}</b></div>
                    <div class="element">Status: <b>{{t.status_retorno ? t.status_retorno : 'aguarde...'}}</b></div>
                    <div class="element">Data: <b>{{dataFormat(t.cadastro)}}</b></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div v-else>
          <div class="field">
            <p class="control">
              <button class="button is-warning button-max" @click="exibirTermos=!exibirTermos">Exibir termos de uso</button>
            </p>
          </div>
        </div>
      </div>
      <div v-if="exibirTermos">
        <div class="modal is-active">
          <div class="modal-background"></div>
          <div class="modal-content">
            <div class="notification is-termo is-my-6">
              <h4><b>Termos de Uso do Chamada Gemidão</b></h4>
              <p>Termos e Condições de Compra e Venda de Chamada Gemidao, com sede na https://chamadagemidao.com, doravante denominada simplesmente Chamada Gemidao, e, de outro lado, o cliente Chamada Gemidao, qualificado no momento da compra dos produtos Chamada Gemidao, doravante denominado simplesmente Cliente.
                <br>
                Considerando que a Chamada Gemidao realiza venda de serviços pela internet; Considerando o interesse do Cliente na compra dos serviços oferecidos pelo Chamada Gemidao; O presente contrato tem por finalidade estabelecer as condições gerais de uso e compra de serviços do cliente do site Chamada Gemidao.
                <br>
                <b>I.</b> Confidencialidade: é de responsabilidade da Chamada Gemidao a preservação da confidencialidade de todos os dados e informações fornecidos pelo Cliente no processo de compra".
                <br>
                <b>II.</b> Serviço de Atendimento ao Cliente (SAC): O cliente dispõe desse serviço para sanar suas dúvidas, solucionar eventuais solicitações ou reclamações a respeito do seu pedido ou de qualquer conteúdo disponibilizado no site. O SAC poderá ser acionado por de chat no site.
                <br>
                <b>III.</b> - O Cliente deverá solicitar a devolução através do Serviço de Atendimento ao Cliente (SAC).
                <br>
                <b>IV.</b> - A compra e realizada via PagSeguro por fornecer varias formas de pagamento e segurança.
                <br>
                <b>V.</b> - Em Todas chamadas realizadas é enviado ao destinatário um SMS com o numero real do remetente.
                <br>
                <b>VI.</b> - É de total responsabilidade do cliente (destinatário da mensagem) o ocorrimento de eventuais transtornos envolvendo a pessoa ou pessoas que atenderem a ligação proveniente deste serviço.
            </p>
              <div class="field is-grouped">
                <p class="control">
                  <button class="button is-success" @click="setTermos(true);">Aceitar</button>
                </p>
                <p class="control">
                  <button class="button is-success" @click="exibirTermos=!exibirTermos">Recusar</button>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import http from '@/services/http';
  import toast from '@/services/toast'
  import moment from 'moment'

  export default {
    name: 'chamadaDoGemidao',
    data () {
      return {
        planoEscolhido: 1,
        planos: {
          1: '1,19', 2: '1,90', 3: '2,60', 4: '3,30', 5: '4,00',
          6: '4,70', 7: '5,40', 8: '6,10', 9: '6,80', 10: '7,50'
        },
        meuNumero: '',
        meuNumero2: '',
        comprando: false,
        auth: {
          numero: '',
          senha: '',
          autenticando: false,
          exibir: false,
          token: '',
        },
        transacoes: [],
        info: {},
        infoAtualizando: false,
        chamada: {
          de: '',
          para: '',
          enviando: false
        },
        exibirTermos: false,
      }
    },

    methods: {
      dataFormat: function (d) {
        return moment.unix(d).format('DD/MM/YY HH:mm:ss')
      },
      removerAutenticacao: function () {
        window.localStorage.removeItem('token');
        this.auth.token='';
        this.info = {};
        this.transacoes = [];
      },
      pagseguro: function (code) {
        PagSeguroLightbox({
          code: code,

        }, {
          success : transactionCode => {
            http.get('/transacao?transacao_id=' + transactionCode)
          },
          abort : function() {
            console.log('abortado')
          }
        })
      },

      comprar: function () {
          http.post('/comprar', {numero: this.meuNumero, plano: this.planoEscolhido}).then(r => {
              if (r.data.code) {
                  this.pagseguro(r.data.code)
              }
              this.comprando = false;
          })
      },

      autenticar: function () {
          this.auth.autenticando = true;
          http.post('/login', {username: this.auth.numero, password: this.auth.senha}).then(r => {
            if (r.data.token) {
                localStorage.setItem("token", r.data.token)
                localStorage.setItem("tokenExpire", r.data.expire)
                window.location.reload();
            }
            return;
          }).catch(err => {
              toast.error('Numero ou senha incorreto');
              return;
          }).then(r => {
            this.auth.autenticando = false;
          })
      },

      getTransacoes: function () {
          http.get('/auth/chamadas/historico').then(r => {
              this.transacoes = r.data
          }).catch(err => {
              // token sem permissao
          })
      },

      getInfo: function () {
          this.infoAtualizando = true
          http.get('/auth/info').then(r => {
              this.info = r.data;
              return;
          }).catch(err => {
              toast.error('Não foi possivel obter as informações');
              return;
          }).then(() => {
            this.infoAtualizando = false;
        });

      },

      ligar: function () {
          this.chamada.enviando = true;
          http.post('/auth/chamada', {de: this.chamada.de, para: this.chamada.para}).then(r => {
              if (r.data.status == 'ok') {
                  toast.success(r.data.message);
                  if (this.info.saldo > 0) {
                      this.info.saldo = this.info.saldo - 1;
                  }
                  return;
              }
              toast.error(r.data.message)
              return;
          }).catch(() => {
              toast.success('Algum problema com a rede ou o serviço, aguarde alguns instantes e tente novamente')
              return;
          }).then(() => {this.chamada.enviando = false;})
      },

      eventoGa: function (categoria, acao) {
        ga('send','event', categoria, acao);
      },

      getTermos: function () {
        return window.localStorage.getItem('termos')
      },
      setTermos: function (status) {
        window.localStorage.setItem('termos', status);
        window.location.reload();
      }
    },

    created: function () {
      let token = localStorage.getItem('token');
      if (token) {
        this.auth.token = token;
        this.getTransacoes();
        this.getInfo();
      }
    }
  }
</script>

<style scoped>
.section {
  padding: 1rem 1.5rem;
}

.element {
  margin-left: 1rem;
  font-size: .9rem;
  /*font-weight: bold;*/
  display: inline-block;
  max-width: 10rem;
  min-width: 2rem;
  text-align: center;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}

.is-termo {
  text-align: left;
}

@media screen and (max-width: 768px) {
  .element {
    font-size: .75rem;
    max-width: 5rem;
  }

  .is-my-6 {
    font-size: .8rem;
  }

  .button-max {
    width: 100%;
  }
}
</style>



